import React from "react";
import PropTypes from "prop-types";
import marked from "marked";
import { debugComponent } from "../../../../lib/logger";
import Swiper from "../Swiper";

const RichTextContent = props => {
  const { onClose } = props;
  if (!props.content) return null;
  const handleSwipe = (direction, movement) => {
    if (direction === "vertical" && movement > 0) {
      onClose();
    }
    debugComponent("MultipleImageContent.swipeTriggered", 7);
  };
  // TODO: clean this
  const content = props.content.replace(/http:\/\/localhost:1337/gi, "");
  return (
    <>
      <div className="richtext-content">
        {props.title ? <h1>{props.title}</h1> : null}
        <Swiper
          id={`post-swiper-${props.id}`}
          onSwipeTriggered={handleSwipe}
          swipeTriggerDirection="vertical"
          swipeTriggerLength={100}
        >
          <div
            dangerouslySetInnerHTML={{
              __html: marked(content, { baseUrl: process.env.FILE_URL })
            }}
          />
        </Swiper>
      </div>
      <style global jsx>{`
        .richtext-content {
          box-sizing: border-box;
          padding: 29px;
        }
        .richtext-content,
        .richtext-content p {
          color: #414145;
          font-family: "Open Sans", sans-serif;
          font-size: 13px;
          letter-spacing: -0.19px;
          line-height: 21px;
        }
        .richtext-content img {
          max-width: 100%;
        }
        .richtext-content h1,
        .richtext-content h2,
        .richtext-content h3,
        .richtext-content h4,
        .richtext-content h5,
        .richtext-content h6 {
          margin: 0;
          padding: 0;
          min-height: 23px;
          color: #262a2c;
          font-family: "Roboto", sans-serif;
          font-size: 19px;
          font-weight: bold;
          letter-spacing: -0.27px;
          line-height: 23px;
          margin: 4px 0;
        }
        .richtext-content h1 {
          margin-bottom: 20px;
        }
      `}</style>
    </>
  );
};

export default React.memo(RichTextContent);
