import React, { useCallback } from "react";
import PropTypes from "prop-types";
import marked from "marked";
import { debugComponent } from "../../../../lib/logger";
import Swiper from "../Swiper";

const OverviewContent = props => {
  const { onClose } = props;
  if (!props.content) return null;
  const handleSwipe = useCallback((direction, movement) => {
    if (direction === "vertical" && movement > 0) {
      onClose();
    }
  }, []);
  debugComponent("<OverviewContent />");
  // TODO: clean this
  const content = props.content.replace(/http:\/\/localhost:1337/gi, "");
  return (
    <>
      <div className="overview-content">
        {props.title ? <h1>{props.title}</h1> : null}
        <Swiper
          id={`overview-swiper-${props.id}`}
          onSwipeTriggered={handleSwipe}
          swipeTriggerDirection="vertical"
          swipeTriggerLength={100}
        >
          <div
            dangerouslySetInnerHTML={{
              __html: marked(content, { baseUrl: process.env.FILE_URL })
            }}
          />
        </Swiper>
      </div>
      <style global jsx>{`
        .overview-content {
          box-sizing: border-box;
          padding: 29px;
        }

        .overview-content,
        .overview-content p {
          color: #414145;
          font-family: "Open Sans", sans-serif;
          font-size: 13px;
          letter-spacing: -0.19px;
          line-height: 21px;
        }
        .overview-content img {
          max-width: 100%;
        }
        .overview-content h1,
        .overview-content h2,
        .overview-content h3,
        .overview-content h4,
        .overview-content h5,
        .overview-content h6 {
          margin: 0;
          padding: 0;
          min-height: 23px;
          color: #262a2c;
          font-family: "Roboto", sans-serif;
          font-size: 19px;
          font-weight: bold;
          letter-spacing: -0.27px;
          line-height: 23px;
          margin: 4px 0;
        }
        .overview-content h1 {
          margin-bottom: 20px;
        }
      `}</style>
    </>
  );
};

export default React.memo(OverviewContent, (prev, next) => {
  // Only re-render on pdf url changed
  return prev.content === next.content;
});
