import React, { useContext, useState, useCallback } from "react";
import PropTypes from "prop-types";
import { ShowcaseContext } from "../ShowcaseContext";
import Star from "./svg/Star";
import Handset from "./svg/Handset";
import Person from "./svg/Person";
import { debugComponent } from "../../../lib/logger";
import posed from "react-pose";
import Modal from "./Modal";

const Box = posed.div({
  pressable: true,
  init: { scale: 1 },
  press: { scale: 0.7 }
});

const CompanyInfo = React.memo(props => {
  const { fav, setFav } = useContext(ShowcaseContext);
  const { name, city, country, keywords, toggleModal, contactPersonMobile } = props;
  const toggleFav = e => {
    setFav(e.target.checked);
  };
  const logo =
    props.logo && props.logo.indexOf("http") !== 0
      ? process.env.FILE_URL + props.logo
      : null;
  debugComponent("<CompanyInfo />", 4); // Perfornmace checking if you need to check that your component doing unwanted render
  return (
    <>
      <div className="company">
        {logo ? (
          <div className="logo">
            <img src={logo} />
          </div>
        ) : null}
        <div>
          <div className="company-name">
            <h1>{name}</h1>
            <Box>
              <label>
                <input onChange={toggleFav} checked={fav} type="checkbox" />
                <Star active={fav} />
              </label>
            </Box>
          </div>
          {country && country ? (
            <h2>
              {country} {country && city ? "|" : " "} {city}
            </h2>
          ) : null}
          <h3>{keywords && keywords.join(", ")}</h3>
          <div>
            <div className="actions">
              <a href={`tel:${contactPersonMobile}`}>
                <Handset /> <span>Call</span>
              </a>
              <Box>
                <button onClick={toggleModal} className="icon">
                  <Person />
                </button>
              </Box>
            </div>
          </div>
        </div>
      </div>

      <style jsx>{`
        .company {
          margin: 10px 18px;
          display: flex;
          flex-direction: row;
        }
        .company-name {
          display: flex;
          flex-direction: row;
        }
        .logo {
          width: 90px;
          height: 90px;
          margin-right: 20px;
        }
        .actions {
          display: flex;
          flex-direction: row;
          align-items: center;
          margin-bottom: 5px;
        }
        h1 {
          margin: 0 0 3px 0;
          padding: 0;
          min-height: 30px;
          color: #262a2c;
          font-family: Roboto, sans-serif;
          font-size: 26px;
          font-weight: bold;
          letter-spacing: -0.74px;
          line-height: 30px;
          margin-right: 12px;
        }
        h2 {
          margin: 0 0 6px 0;
          padding: 0;
          height: 18px;
          color: #8d969a;
          font-family: Roboto, sans-serif;
          font-size: 15px;
          font-weight: 500;
          line-height: 18px;
        }
        h3 {
          margin: 0 0 12px 0;
          padding: 0;
          height: 14px;
          color: #8d969a;
          font-family: Roboto, sans-serif;
          font-size: 12px;
          line-height: 14px;
        }
        label {
          font-size: 0;
          overflow: hidden;
          height: 30px;
          width: 30px;
          position: relative;
          display: flex;
          align-items: center;
        }
        label input[type="checkbox"] {
          position: absolute;
          outline: none;
          width: 30px;
          height: 30px;
          padding: 0;
          margin: 0;
          opacity: 0;
          background-color: transparent;
          border: none;
        }
        a,
        button {
          padding: 7px 24px;
          height: 34px;
          box-sizing: border-box;
          border: none;
          border-radius: 18.5px;
          background-color: #1640e8;
          color: #ffffff;
          font-family: "Roboto", sans-serif;
          font-size: 18px;
          font-weight: 500;
          line-height: 21px;
          text-align: center;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          margin-right: 11px;
          text-decoration: none;
        }
        a span,
        button span {
          margin-left: 4px;
        }
        a.icon,
        button.icon {
          padding: 0;
          width: 34px;
        }
        div img {
          max-width: 100%;
        }
      `}</style>
    </>
  );
});

const Company = props => {
  const {
    avatar,
    id,
    name,
    city,
    country,
    address,
    contactPersonMobile,
    contactPersonFirstName,
    contactPersonLastName,
    website
  } = props;
  const [modal, setModal] = useState();
  debugComponent("<Company />", 4); // Perfornmace checking if you need to check that your component doing unwanted render
  const showModal = useCallback(() => {
    setModal(true);
  }, []);
  const hideModal = useCallback(() => {
    setModal(false);
  }, []);
  return (
    <>
      <CompanyInfo {...props} toggleModal={showModal} />
      <Modal
        onClose={hideModal}
        content={{
          avatar,
          id,
          name,
          city,
          country,
          address,
          contactPersonMobile,
          contactPersonFirstName,
          contactPersonLastName,
          website,
          type: "Profile"
        }}
        visible={modal}
      />
    </>
  );
};

Company.propTypes = {
  name: PropTypes.string.isRequired,
  logo: PropTypes.string,
  country: PropTypes.string,
  city: PropTypes.string,
  keywords: PropTypes.array
};
Company.defaultProps = {
  keywords: []
};

export default React.memo(Company);
