import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import Tile from "./Tile";
import { debugComponent } from "../../../lib/logger";

/**
 * This component is used to render list of `<Tile/>` component, it doesnt mater what type it is
 * Optimize rendering only when property changes using PureComponent
 * NOTE: This component only depend on propTypes changes to re-render
 * @version 1.0.0
 */
export default class Tiles extends PureComponent {
  static propTypes = {
    /**
     * Array of contents
     * Can be Blocks/Tiles
     * Come from Exhibitor.contents
     */
    tiles: PropTypes.array
  };
  static defaultProps = {
    /**
     * Always set this to empty array
     */
    tiles: []
  };
  render() {
    // Perfornmace checking if you need to check that your component doing unwanted render
    debugComponent("<Tiles />", 6);
    const { tiles } = this.props;
    return (
      <>
        <div className="tiles">
          {tiles.map((x, i) => (
            <Tile
              key={x.id}
              big={x.largeTile || (i === tiles.length - 1 && i % 2 === 0)}
              {...x}
            />
          ))}
        </div>
        <style jsx>{`
          div.tiles {
            margin-left: 18px;
            margin-top: 18px;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
          }
        `}</style>
      </>
    );
  }
}
