import React, { useState, useLayoutEffect, useCallback } from "react";
import PropTypes from "prop-types";
import { debugComponent } from "../../../../lib/logger";
import Close from "../svg/Close";
import Swiper from "../Swiper";
import Download from "../svg/Download";

const SingleImageContent = props => {
  const [afterShow, setAfterShow] = useState(false);
  const { visible, onClose } = props;
  useLayoutEffect(() => {
    if (visible) {
      const id = setTimeout(() => {
        setAfterShow(visible);
      }, 1000);
      return () => {
        clearTimeout(id);
      };
    } else {
      setAfterShow(visible);
    }
  }, [visible]);
  const src =
    !props.url || props.url.indexOf("http") === 0
      ? props.url
      : `${process.env.FILE_URL}${props.url}`;
  const handleClick = () => {
    onClose();
  };
  const handleSwipe = useCallback((direction, movement) => {
    if (direction === "vertical") {
      onClose();
    }
  }, []);
  debugComponent("<SingleImageContent />");
  return (
    <>
      <div className="single-image-content">
        <img src={src} alt={props.title} />
        <div className="caption">{props.title}</div>
        <Swiper
          id={`single-image-swiper-${props.id}`}
          onSwipeTriggered={handleSwipe}
          swipeTriggerDirection="vertical"
          swipeTriggerLength={100}
        />
        <button onClick={handleClick} className={afterShow ? "show" : ""}>
          <Close />
        </button>
        <a
          href={src}
          download
          target="_blank"
          type="application/octet-stream"
          className={afterShow ? "show" : ""}
        >
          <Download />
        </a>
      </div>
      <style jsx>{`
        div.single-image-content {
          box-sizing: border-box;
          background: #000 url(${src}) no-repeat center center;
          background-size: contain;
          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          right: 0;
          margin-left: -10px;
          margin-right: -10px;
          margin-top: -34px;
        }
        .caption {
          box-sizing: border-box;
          position: absolute;
          left: 0;
          bottom: 0;
          right: 0;
          min-height: 163px;
          padding: 20px;
          padding-bottom: 100px;
          z-index: 1;
          display: flex;
          align-items: center;
          color: #fff;
          font-family: "Open Sans", sans-serif;
          font-size: 13px;
          letter-spacing: -0.19px;
          line-height: 21px;
          background: linear-gradient(
            to bottom,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.49) 10%,
            rgba(0, 0, 0, 0.82) 100%
          );
        }
        a,
        button {
          opacity: 0;
          transform: scale(0, 0) rotate(180deg);
          position: absolute;
          top: 0;
          right: 0;
          margin-right: 25px;
          z-index: 1000;
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
          margin-top: 23px;
          overflow: hidden;
          border-radius: 50%;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          height: 36px;
          width: 36px;
          box-sizing: border-box;
          background-color: #fff;
          border: none;
          z-index: 5;
        }
        a {
          margin-left: -18px;
          margin-right: auto;
          top: auto;
          left: 50%;
          bottom: 26px;
        }
        a.show,
        button.show {
          transition: all linear 300ms;
          transform: scale(1, 1) rotate(0deg);
          opacity: 1;
        }
      `}</style>
      <style global jsx>{`
        .single-image-content img {
          width: 100%;
          height: 100%;
          opacity: 0;
        }
      `}</style>
    </>
  );
};

export default React.memo(SingleImageContent);
