import React, { useState } from "react";
import PropTypes from "prop-types";
import { ShowcaseProvider } from "./ShowcaseContext";
import Template from "./components/Template";
import Head from "next/head";
import { debugComponent } from "../../lib/logger";
import { Preload } from "./components/Preload";

/**
 * This is the Showcase Main Component
 * This is the main Component being imported to the router
 * The component name should describe the feature
 * @param {object} props
 * @param {object} props.exhibitor
 * @param {Array} props.exhibitor.contents
 * @version 1.0.0
 */
export const Showcase = React.memo(props => {
  /**
   * The data to feed the template
   * This is passed on Server Side Rendering via Appolo GraphQL
   */
  const { exhibitor } = props;
  /**
   * State used in Showcase Context, fav and setFav is being passed to
   * ```jsx
   * <ShowcaseContextProvider value={{fav, setFav}} />
   * ```
   * @todo: please move this down to the `Company.js` component level if no longer needed for global
   *  in the future, we can use `redux`when global state/store get complicated
   */
  const [fav, setFav] = useState(false);
  // TODO - Redux / Reducer
  const context = {
    fav,
    setFav
  };
  // Perfornmace checking if you need to check that your component doing unwanted render
  debugComponent("<Showcase />", 2);
  /**
   * This component use `css-in-js` which is `nextjs styled jsx`
   * It is best practive to separate the `<styles/>` that has changing styles for optimization
   * @see https://github.com/zeit/styled-jsx
   */
  return (
    <div className="showcase">
      <Head>
        <meta
          key="viewport"
          name="viewport"
          content="width=device-width,initial-scale=1,max-scale=1"
        />
        <Preload
          images={
            exhibitor
              ? [
                  ...exhibitor.contents
                    .map(x => x.image.map(i => i.url))
                    .reduce((images, image) => [...images, ...image], []),
                  ...(exhibitor.banner ? [exhibitor.banner.url] : []),
                  ...(exhibitor.logo ? [exhibitor.logo.url] : []),
                  ...(exhibitor.contactPersonPhoto
                    ? [exhibitor.contactPersonPhoto.url]
                    : [])
                ]
              : []
          }
          pdfs={
            exhibitor
              ? exhibitor.contents.filter(x => x.pdf).map(i => i.pdf.url)
              : []
          }
        />
        <title key="title">
          {exhibitor ? `${exhibitor.companyName} | ` : ""}GEVME Showcase
        </title>
      </Head>
      <ShowcaseProvider value={context}>
        <Template exhibitor={exhibitor} />
      </ShowcaseProvider>
      <style global jsx>{`
        .showcase {
          background-color: ${exhibitor && exhibitor.banner
            ? "transparent"
            : "#f1f5f9"};
        }
      `}</style>
      <style global jsx>{`
        @font-face {
          font-family: "Roboto";
          font-style: normal;
          font-weight: 400;
          font-display: auto;
          src: local("Roboto"), local("Roboto-Regular"),
            url("/static/fonts/Roboto-Regular.woff2") format("woff2"),
            url("/static/fonts/Roboto-Regular.woff") format("woff");
        }
        @font-face {
          font-family: "Roboto";
          font-weight: 700;
          font-style: normal;
          font-display: auto;
          src: local("Roboto Bold"), local("Roboto-Bold"),
            url("/static/fonts/Roboto-Bold.woff2") format("woff2"),
            url("/static/fonts/Roboto-Bold.woff") format("woff");
        }
        @font-face {
          font-family: "Open Sans";
          font-style: normal;
          font-weight: 400;
          src: local("Open Sans Regular"), local("OpenSans-Regular"),
            url("/static/fonts/OpenSans-Regular.woff2") format("woff2"),
            url("/static/fonts/OpenSans-Regular.woff") format("woff");
        }
        @font-face {
          font-family: "Open Sans";
          font-weight: 700;
          font-style: normal;
          src: local("Open Sans Bold"), local("OpenSans-Bold"),
            url("/static/fonts/OpenSans-Bold.woff2") format("woff2"),
            url("/static/fonts/OpenSans-Bold.woff") format("woff");
        }
        body {
          padding: 0;
          margin: 0;
          margin: auto;
          font-family: "Open Sans", sans-serif;
          overscroll-behavior: contain;
        }
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
          font-family: "Roboto", sans-serif;
        }
        .showcase {
          margin: auto;
          height: auto;
          overflow-y: auto;
          max-width: 441px;
        }
        @media only screen and (max-width: 768px) {
          .showcase {
            max-width: 100%;
          }
        }
        button,
        a {
          outline: none;
        }
      `}</style>
    </div>
  );
});

Showcase.propTypes = {
  exhibitor: PropTypes.object
};
