import React, { useState, useLayoutEffect } from "react";
import { createPortal } from "react-dom";
import ArrowDown from "./svg/ArrowDown";
import OverviewContent from "./content/OverviewContent";
import SingleImageContent from "./content/SingleImageContent";
import RichTextContent from "./content/RichTextContent";
import PdfContent from "./content/PdfContent";
import VideoContent from "./content/VideoContent";
import { debugComponent } from "../../../lib/logger";
import MultipleImageContent from "./content/MultipleImageContent";
import ProfileContent from "./content/ProfileContent";

/**
 * This need the `<div id="modal"/>` in `_document.js`
 * Also, this will not SSR
 * @param {*} props
 */
const ModalPortal = props => {
  const [mounted, setMounted] = useState(false);
  const [visible, setVisible] = useState(false);
  useLayoutEffect(() => {
    setMounted(true);
  }, []);
  useLayoutEffect(() => {
    let timer1;
    if (props.visible) {
      setVisible(true);
    } else {
      timer1 = setTimeout(() => {
        props.onMounted(props.visible);
      }, 200);
    }
    let timer2 =setTimeout(() => {
      props.onMounted(props.visible);
    }, 10);
    return ()=>{
      if(timer1) clearTimeout(timer1);
      if(timer2) clearTimeout(timer2);
    }
  }, [props.visible]);
  return mounted
    ? createPortal(
        <div style={{ display: visible ? "" : "none" }}>{props.children}</div>,
        document.getElementById("modal")
      )
    : null;
};

/**
 * Custom Modal Componet
 * @param {*} props
 */
const Modal = props => {
  const { content, visible, onClose } = props;
  const [mounted, setMounted] = useState(false);
  /**
   * common props to be passed on each content
   */
  const obj = { onClose, id: content.id, visible: mounted };
  // Perfornmace checking if you need to check that your component doing unwanted render
  debugComponent("<Modal />", 4, props);
  return (
    <>
      {typeof window !== "undefined" ? (
        <ModalPortal visible={visible} onMounted={setMounted}>
          <div className={`modal ${mounted ? "open" : ""}`}>
            <div onClick={onClose} className="backdrop" />
            <div className={`container ${mounted ? "visible" : ""}`}>
              <div className="inner">
                <div onClick={onClose} className="arrow">
                  <ArrowDown />
                </div>
                <div className="content">
                  {content && content.type === "Pdf" ? (
                    <PdfContent
                      {...obj}
                      pdf={content.pdf ? content.pdf.url : null}
                    />
                  ) : null}
                  {content && (content.type === "Post" || !content.type) ? (
                    <RichTextContent
                      {...obj}
                      title={content.title}
                      content={content.textContent}
                    />
                  ) : null}
                  {content && content.type === "Overview" ? (
                    <OverviewContent
                      {...obj}
                      title={content.title}
                      content={content.textContent}
                    />
                  ) : null}
                  {content && content.type === "Profile" ? (
                    <ProfileContent {...obj} {...content} />
                  ) : null}
                  {content &&
                  content.type === "Image" &&
                  content.image &&
                  content.image.length === 1 ? (
                    <SingleImageContent
                      {...obj}
                      title={content.title}
                      url={content.image ? content.image[0].url : ""}
                    />
                  ) : null}
                  {content &&
                  content.type === "Image" &&
                  content.image &&
                  content.image.length > 1 ? (
                    <MultipleImageContent
                      {...obj}
                      title={content.title}
                      urls={content.image.map(x => x.url)}
                    />
                  ) : null}
                  {content && content.type === "Video" ? (
                    <VideoContent
                      {...obj}
                      title={content.title}
                      url={content.url || ""}
                    />
                  ) : null}
                </div>
              </div>
            </div>
          </div>
        </ModalPortal>
      ) : null}
      <style global jsx>{`
        html,
        body {
          overflow: ${!mounted ? "" : "hidden"};
        }
        #modal {
          position: relative;
          top: 0;
          margin: auto;
          z-index: 100;
          max-width: 441px;
        }
        @media only screen and (max-width: 768px) {
          #modal {
            max-width: 100%;
          }
        }
      `}</style>
      <style jsx>{`
        .modal {
          box-sizing: border-box;
          transition: all ease-in-out 200ms;
          top: 100vh;
          z-index: 100;
          height: 100vh;
          position: fixed;
          width: 441px;
          max-width: 100%;
          opacity: 0;
        }
        .modal.open {
          top: 0;
          opacity: 1;
        }
        .modal .container {
          position: relative;
          box-sizing: border-box;
          height: 100vh;
          top: 0;
          margin: 0 10px;
          display: block;
          width: auto;
          max-width: 100%;
        }
        .modal .backdrop {
          background-color: #000;
          opacity: 0;
          position: absolute;
          left: 0;
          top: 0;
          height: 100vh;
          z-index: 0;
          width: 441px;
          max-width: 100%;
        }
        .modal.open .backdrop {
          top: 0;
          opacity: 0.35;
          transition: opacity ease-in-out 400ms;
        }
        .modal .container.visible {
          top: 0;
        }
        .inner {
          position: relative;
          z-index: 1;
          box-sizing: border-box;
          background: #fff;
          box-shadow: 0 0 45px 0 rgba(0, 0, 0, 0.25);
          border-radius: 19px 19px 0 0;
          display: block;
          box-sizing: border-box;
          margin-top: 33px;
          min-height: calc(100vh - 33px);
        }
        .arrow {
          border: none;
          background-color: transparent;
          display: flex;
          padding: 16px 0;
          flex-direction: row;
          align-items: center;
          justify-content: center;
        }
        .content {
          overflow-y: auto;
          height: calc(100vh - 43px - 33px - 20px);
          max-height: calc(100vh - 43px - 33px - 20px);
        }
        @media only screen and (max-width: 768px) {
          .modal .backdrop,
          .modal .container,
          .modal {
            max-width: 100%;
          }
        }
      `}</style>
    </>
  );
};

export default Modal;
