import React, {
  useState,
  useCallback,
  useRef,
  useMemo,
  useLayoutEffect
} from "react";
import PropTypes from "prop-types";
import classnames from "classnames";
import { debugComponent } from "../../../../lib/logger";
import Close from "../svg/Close";
import Download from "../svg/Download";
import Swiper from "../Swiper";

const MultipleImageContent = props => {
  const [afterShow, setAfterShow] = useState(false);
  const [active, setActive] = useState(0);
  const { visible, onClose } = props;
  const sliderRef = useRef();
  useLayoutEffect(() => {
    if (visible) {
      const id = setTimeout(() => {
        setAfterShow(visible);
      }, 1000);
      return () => {
        clearTimeout(id);
      };
    } else {
      setAfterShow(visible);
    }
  }, [visible]);
  const images = useMemo(() => {
    return props.urls.map(u =>
      u && u.indexOf("http") === 0 ? u : `${process.env.FILE_URL}${u}`
    );
  }, [props.urls.length]);
  const handleSwipe = useCallback(
    (direction, movement) => {
      if (direction === "horizontal" && movement > 0) {
        setActive(active - 1 < 0 ? images.length - 1 : active - 1);
      }
      if (
        typeof direction !== "string" ||
        (direction === "horizontal" && movement < 0)
      ) {
        setActive((active + 1) % images.length);
      }
      if (direction === "vertical") {
        onClose();
      }
      debugComponent("MultipleImageContent.swipeTriggered", 7);
    },
    [active, images.length, onClose]
  );
  debugComponent("<MultipleImageContent />");
  const handleClick = useCallback(() => {
    onClose();
  }, []);
  return (
    <>
      <div ref={sliderRef} className="multiple-image-content">
        {images.map((src, i) => (
          <div
            key={src}
            className={classnames("image", {
              active: active === i,
              prev: (active - 1 < 0 ? images.length - 1 : active - 1) === i,
              next: (active + 1) % images.length === i
            })}
            style={{ backgroundImage: `url(${src})` }}
          >
            <img src={src} alt={props.title} />
          </div>
        ))}
        <div className="dots">
          {images.map((src, i) => (
            <div
              key={src}
              className={classnames("dot", {
                active: active === i,
                prev: (active - 1 < 0 ? images.length - 1 : active - 1) === i,
                next: (active + 1) % images.length === i
              })}
            />
          ))}
        </div>
        <Swiper
          id={`multiple-image-swiper-${props.id}`}
          onClick={handleSwipe}
          onSwipeTriggered={handleSwipe}
          swipeTriggerDirection="both"
          swipeTriggerLength={100}
        />
        <button onClick={handleClick} className={afterShow ? "show" : ""}>
          <Close />
        </button>
        <a
          href={images[active]}
          download
          target="_blank"
          type="application/octet-stream"
          className={afterShow ? "show" : ""}
        >
          <Download />
        </a>
      </div>
      <style jsx>{`
        .multiple-image-content {
          box-sizing: border-box;
          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          right: 0;
          margin-left: -10px;
          margin-right: -10px;
          margin-top: -34px;
          overflow: hidden;
          background: #000;
        }
        .multiple-image-content > div.image {
          background: #000 none no-repeat center center;
          background-size: contain;
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          display: none;
        }
        .multiple-image-content > div.dots {
          position: absolute;
          height: 6px;
          width: 100%;
          left: 0;
          bottom: 80px;
          display: flex;
          flex-direction: row;
          justify-content: center;
          pointer-events: none;
        }
        .multiple-image-content > div.dots > div.dot {
          width: 6px;
          height: 6px;
          min-height: 6px;
          min-width: 6px;
          overflow: hidden;
          margin: 0 2px;
          border-radius: 50%;
          background-color: rgba(255, 255, 255, 0.3);
          box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.2);
          z-index: 2;
        }
        .multiple-image-content > div.dots > div.dot.active {
          background-color: rgba(255, 255, 255, 1);
          box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.8);
        }
        .multiple-image-content > div.image.prev {
          z-index: 1;
          transition: transform linear 200ms;
          transform: scale(0.6) translateX(-100%);
          display: block;
        }
        .multiple-image-content > div.image.active {
          z-index: 2;
          transition: transform linear 200ms;
          transform: scale(1) translateX(0);
          display: block;
        }
        .multiple-image-content > div.image.next {
          z-index: 1;
          transition: transform linear 200ms;
          transform: scale(0.6) translateX(100%);
          display: block;
        }
        a,
        button {
          padding: 0;
          opacity: 0;
          transform: scale(0, 0);
          position: absolute;
          top: 0;
          right: 0;
          margin-right: 25px;
          z-index: 1000;
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
          margin-top: 23px;
          overflow: hidden;
          border-radius: 50%;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          height: 36px;
          width: 36px;
          box-sizing: border-box;
          background-color: #fff;
          border: none;
          z-index: 5;
        }
        a {
          margin-left: -18px;
          margin-right: auto;
          top: auto;
          left: 50%;
          bottom: 26px;
        }
        a.show,
        button.show {
          transition: all linear 300ms;
          transform: scale(1, 1);
          opacity: 1;
        }
      `}</style>
      <style global jsx>{`
        .multiple-image-content img {
          width: 100%;
          height: 100%;
          opacity: 0;
        }
      `}</style>
    </>
  );
};

export default React.memo(MultipleImageContent);
