import React from "react";

export const Preload = React.memo(props => {
  const images = (props.images || []).map(x => {
    if (x.indexOf("http") === 0) {
      return x;
    }
    return `${process.env.FILE_URL}${x}`;
  });
  const pdfs = (props.pdfs || []).map(x => {
    if (x.indexOf("http") === 0) {
      return x;
    }
    return `${process.env.FILE_URL}${x}`;
  });
  return (
    <>
      {images.map(x => (
        <link key={x} rel="preload" href={x} as="image" />
      ))}
      {pdfs.map(x => (
        <link key={x} rel="preload" href={x} as="document" />
      ))}
      <link
        rel="preload"
        href="/static/fonts/OpenSans-Regular.woff2"
        as="font"
        type="font/woff2"
        crossorigin
      />
      <link
        rel="preload"
        href="/static/fonts/Roboto-Regular.woff2"
        as="font"
        type="font/woff2"
        crossorigin
      />
    </>
  );
});
