/**
 * @description
 * This is use to maintain aspec ratio of the thumbnail without specifying the width and height
 */
import React from "react";

const Thumbnail = ({ width, height, image, ...props }) => (
  <svg
    className="tile-thumbnail"
    style={{ backgroundImage: `url(${image || "/static/svg/placeholder.svg"})` }}
    width={width || "100%"}
    height={height || undefined}
    viewBox="0 0 160 120"
    {...props}
  ></svg>
);

export default React.memo(Thumbnail);
