import React from "react";
import { useQuery } from "@apollo/react-hooks";
import Showcase from "../../components/showcase";
import { getExhibitor } from "../../lib/query/getExhibitor";
import { debugComponent } from "../../lib/logger";

/**
 * This Component is for routing, should do less logic other than preparing Data for server-side-rendering
 * Should compute data that will never be changed unless page reloaded/route changed
 */
const Content = props => {
  const query = props.query;
  if(!query.slug) return null;
  const { data, error, loading } = useQuery(getExhibitor, {
    variables: {
      id: query.slug,
      notifyOnNetworkStatusChange: true
    }
  });
  const { exhibitor } = data || {};

  // Perfornmace checking if you need to check that your component doing unwanted render
  debugComponent("<Content />", 1);
  return (
    <>
      {/* TODO: Preloading <link rel="preload" as="image" href /> */}
      <Showcase loading={loading} error={error} exhibitor={exhibitor} />
    </>
  );
};

/**
 * This run on the server rendering only
 */
Content.getInitialProps = ({ query, req }) => {
  // const userAgent = req.headers["user-agent"] || (typeof navigator !== "undefined" && navigator.userAgent);
  return {
    query: query,
    // userAgent // future reference for iPad, iPhone or Android
  };
};

export default Content;
