import React, { useState, useCallback } from "react";
import PropTypes from "prop-types";
import { Document, Page } from "react-pdf";
import { debugComponent } from "../../../../lib/logger";
import Swiper from "../Swiper";
import Download from "../svg/Download";

const PdfContent = props => {
  const { onClose } = props;
  const [pages, setPages] = useState(0);
  const handleDocumentLoadSuccess = useCallback(
    ({ numPages }) => {
      debugComponent("<PdfContent Success />", numPages);
      setPages(numPages);
    },
    [pages]
  );
  const handleSwipe = useCallback((direction, movement) => {
    if (direction === "vertical" && movement > 0) {
      onClose();
    }
  }, []);
  const url =
    props.pdf.indexOf("http") === 0
      ? props.pdf
      : `${process.env.FILE_URL}${props.pdf}`;
  debugComponent("<PdfContent />", 2, { pages, props });
  if (!props.pdf) return null;
  return (
    <>
      <div className="pdf-content">
        <Swiper
          id={`pdf-swiper-${props.id}`}
          onSwipeTriggered={handleSwipe}
          swipeTriggerDirection="vertical"
          swipeTriggerLength={100}
        >
          <Document file={url} onLoadSuccess={handleDocumentLoadSuccess}>
            {Array.from(new Array(pages), (el, i) => (
              <Page
                width={400}
                renderAnnotationLayer={false}
                renderTextLayer={false}
                key={`page_${i + 1}`}
                pageNumber={i + 1}
              />
            ))}
          </Document>
        </Swiper>
        <a
          href={url}
          download
          target="_blank"
          type="application/octet-stream"
          className="btn"
        >
          <Download />
        </a>
      </div>
      <style jsx>{`
        div {
          box-sizing: border-box;
          padding: 22px;
        }
      `}</style>
      <style global jsx>{`
        .pdf-content iframe {
          width: 100%;
          height: calc(100vh - 43px - 33px - 110px);
          max-height: calc(100vh - 43px - 33px - 110px);
          overflow-y: auto;
          border: none;
        }
        .pdf-content .react-pdf__Document {
          display: flex;
          flex-direction: column;
        }
        .pdf-content .react-pdf__Page {
          height: auto;
          margin-bottom: 20px;
          box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.35);
        }
        .pdf-content canvas {
          max-width: 100%;
          height: auto !important;
        }
        a.btn {
          position: relative;
          left: 50%;
          margin: 20px;
          margin-left: -18px;
          z-index: 1000;
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
          margin-top: 23px;
          overflow: hidden;
          border-radius: 50%;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          height: 36px;
          width: 36px;
          box-sizing: border-box;
          background-color: #fff;
          border: none;
        }
      `}</style>
    </>
  );
};

PdfContent.propTypes = {
  id: PropTypes.string.isRequired,
  pdf: PropTypes.string.isRequired,
  onClose: PropTypes.func.isRequired
};

export default React.memo(PdfContent, (prev, next) => {
  // Only re-render on pdf url changed
  return prev.pdf === next.pdf;
});
