import React, { useState, useCallback } from "react";
import PropTypes from "prop-types";
import { debugComponent } from "../../../lib/logger";
import Modal from "./Modal";

const Overview = props => {
  const { id, title, overview, summary } = props;
  const [modal, setModal] = useState(false);
  const hideModal = useCallback(() => {
    setModal(false);
  }, []);
  const showModal = useCallback(() => {
    setModal(true);
  }, []);
  const content = {
    id: id,
    type: "Overview",
    title: title,
    textContent: overview
  };
  debugComponent("<Overview />", 4); // Perfornmace checking if you need to check that your component doing unwanted render
  return (
    <>
      <article onClick={showModal} className="overview">
        <h2>{title}</h2>
        <p>{summary}</p>
      </article>
      <style jsx>{`
        .overview {
          background-color: #ffffff;
          box-shadow: 0 10px 35px 0 rgba(0, 0, 0, 0.13);
          border-radius: 19px;
          padding: 22px 19px;
          margin: 10px 18px;
        }
        h2 {
          margin: 0 0 5px 0;
          padding: 0;
          height: 23px;
          color: #262a2c;
          font-family: "Roboto", sans-serif;
          font-size: 19px;
          font-weight: bold;
          letter-spacing: -0.27px;
          line-height: 23px;
          margin-botton: 3px;
        }
        p {
          padding: 0;
          margin: 0;
          color: #414145;
          font-family: "Open Sans", sans-serif;
          font-size: 13px;
          line-height: 20px;
        }
      `}</style>
      <Modal onClose={hideModal} content={content} visible={modal} />
    </>
  );
};

Overview.propTypes = {
  title: PropTypes.string,
  overview: PropTypes.string,
  onClick: PropTypes.func
};

export default React.memo(Overview);
