import gql from "graphql-tag";
export const getExhibitor = gql`
  query exhibitor($id: ID!) {
    exhibitor(id: $id) {
      id
      slug
      companyName
      locationCountry
      locationCity
      industry
      overviewHidden
      overviewTitle
      overviewSummary
      overview
      address
      contactPersonMobile
      contactPersonFirstName
      contactPersonLastName
      website
      logo {
        url
      }
      banner {
        url
      }
      contactPersonPhoto {
        url
      }
      contents {
        id
        textContent
        type
        url
        largeTile
        pdf {
          name
          url
          mime
        }
        image {
          name
          url
          mime
        }
        title
      }
    }
  }
`;
