import React, { useRef, useCallback } from "react";
import PropTypes from "prop-types";
import Banner from "./Banner";
import Company from "./Company";
import Overview from "./Overview";
import Tiles from "./Tiles";
import ArrowUp from "./svg/ArrowUp";
import Back from "./svg/Back";
import Footer from "./Footer";
import { debugComponent } from "../../../lib/logger";

/**
 * This is the Template Component for `<Showcase />`
 * @version 1.0.0
 */
const Template = props => {
  const exhibitor = props.exhibitor || {};
  const containerRef = useRef();
  const {
    id,
    address,
    companyName,
    locationCountry,
    locationCity,
    industry,
    overviewHidden,
    overviewTitle,
    overviewSummary,
    overview,
    contactPersonMobile,
    contactPersonFirstName,
    contactPersonLastName,
    website
  } = exhibitor || {};
  /**
   * Because we don't expect the logo is always present
   */
  const logo = (exhibitor.logo && exhibitor.logo.url) || null;
  /**
   * Because we don't expect the avatar is always present
   */
  const avatar =
    (exhibitor.contactPersonPhoto && exhibitor.contactPersonPhoto.url) || null;
  /**
   * Because we don't expect the banner is always present
   */
  const banner = (exhibitor.banner && exhibitor.banner.url) || null;
  /**
   * Its more optimized to set style by ref rather than putting it to state and rendring the component
   */
  const handleBannerResize = useCallback(
    size => {
      if (containerRef.current) {
        containerRef.current.style.transform = `translateY(${size}px)`;
        containerRef.current.style.transition = !size
          ? "all linear 100ms"
          : "none";
      }
    },
    [containerRef.current]
  );
  // Perfornmace checking if you need to check that your component doing unwanted render
  debugComponent("<Template />", 3);
  return (
    <>
      {/* Hide banner component if no banner is present */}
      {(banner || null) && (
        <Banner onResize={handleBannerResize} banner={banner} />
      )}
      <button>
        <Back />
      </button>
      <div ref={containerRef} className="container">
        {/* Hide arrow up when no banner is present */}
        {(banner || null) && (
          <div className="arrow">
            <ArrowUp />
          </div>
        )}
        <Company
          id={id}
          avatar={avatar}
          logo={logo}
          name={companyName}
          city={locationCity}
          address={address}
          country={locationCountry}
          contactPersonMobile={contactPersonMobile}
          contactPersonFirstName={contactPersonFirstName}
          contactPersonLastName={contactPersonLastName}
          website={website}
          keywords={industry ? [industry] : []}
        />
        {(!overviewHidden || null) && (
          <Overview
            id={exhibitor.id}
            title={overviewTitle}
            summary={overviewSummary}
            overview={overview}
          />
        )}
        {(exhibitor.contents || null) && <Tiles tiles={exhibitor.contents} />}
      </div>
      <Footer companyName={companyName} contactPersonMobile={contactPersonMobile} />
      <style jsx>{`
        button {
          position: fixed;
          top: 0;
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
          margin-top: 23px;
          margin-left: 15px;
          overflow: hidden;
          border-radius: 50%;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          height: 36px;
          width: 36px;
          box-sizing: border-box;
          background-color: #ffffff;
          border: none;
          z-index: 5;
        }
        .container {
          border-radius: 30px 30px 0 0;
          box-shadow: 0 10px 35px 0 rgba(0, 0, 0, 0.13);
          background-color: #f1f5f9;
          padding-top: 16px;
          position: relative;
          z-index: 1;
          margin-top: calc(100vh - 200px - 70px);
          padding-bottom: 67px;
        }
        .container .arrow {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
        }
      `}</style>
      {/* Layout based on banner visibility */}
      <style jsx>{`
        .container {
          padding-top: ${banner ? "16px" : "60px"};
          box-shadow: ${banner ? "0 10px 35px 0 rgba(0, 0, 0, 0.13)" : "none"};
          border-radius: ${banner ? "30px 30px 0 0" : "0"};
        }
      `}</style>
    </>
  );
};

Template.propTypes = {
  exhibitor: PropTypes.object
};

export default React.memo(Template);
