import React from "react";
import PropTypes from "prop-types";
import Swiper from "../Swiper";
import { debugComponent } from "../../../../lib/logger";

const ProfileContent = props => {
  const {
    onClose,
    avatar,
    id,
    name,
    city,
    country,
    keywords,
    address,
    contactPersonFirstName,
    contactPersonLastName,
    contactPersonMobile,
    contactPersonEmail,
    website
  } = props;

  const handleSwipe = (direction, movement) => {
    if (direction === "vertical" && movement > 0) {
      onClose();
    }
  };
  // TODO: Clean this
  const logo =
    avatar && avatar.indexOf("http") !== 0
      ? process.env.FILE_URL + avatar
      : null;
  debugComponent("<ProfileContent />", 5);
  return (
    <>
      <div className="profile-content">
        <h1>
          {logo ? <img className="img" src={logo} alt={name} /> : null}{" "}
          {contactPersonFirstName} {contactPersonLastName}
        </h1>
        <ul>
          {address || city ? <li>Address <strong>{address || city}</strong></li> : null}
          {country ? <li>Country <strong>{country}</strong></li> : null}
          {contactPersonMobile ? <li>Phone <strong>{contactPersonMobile}</strong></li> : null}
          {contactPersonEmail ? <li>Email <strong>{contactPersonEmail}</strong></li> : null}
          {website ? <li>Website <strong>{website}</strong></li> : null}
        </ul>
        <Swiper
          id={`profile-swiper-${id}`}
          onClick={onClose}
          onSwipeTriggered={handleSwipe}
          swipeTriggerDirection="vertical"
          swipeTriggerLength={100}
        />
      </div>
      <style global jsx>{`
        .profile-content {
          box-sizing: border-box;
          padding: 29px;
        }

        .profile-content ul,
        .profile-content li {
          list-style: none;
          padding: 0;
          margin: 0;
          margin-bottom: 20px;
        }
        .profile-content,
        .profile-content li {
          color: #414145;
          font-family: "Open Sans", sans-serif;
          font-size: 13px;
          letter-spacing: -0.19px;
          line-height: 21px;
        }
        .profile-content h1 {
          margin: 0;
          padding: 0;
          min-height: 32px;
          color: #262a2c;
          font-family: "Roboto", sans-serif;
          font-size: 27px;
          font-weight: bold;
          letter-spacing: -0.39px;
          line-height: 32px;
          margin: 4px 0;
          margin-bottom: 26px;
          display: flex;
          flex-direction: row;
          align-items: center;
        }
        .profile-content .img {
          width: 34px;
          height: 34px;
          max-width: 34px;
          max-height: 34px;
          margin-right: 10px;
          border-radius: 50%;
          overflow: hidden;
          background: transparent none no-repeat center center;
          background-size: center;
        }
      `}</style>
    </>
  );
};

export default React.memo(ProfileContent, (prev, next) => {
  // Check only id changed, its fine
  return prev.id === next.id;
});
