import React, { useCallback, useRef, useMemo } from "react";
import PropTypes from "prop-types";
import { debugComponent } from "../../../lib/logger";
import { ShowcaseContext } from "../ShowcaseContext";
import Swiper from "./Swiper";
import DocumentScroll from "./DocumentScroll";

const Banner = props => {
  const { onResize } = props;
  const bannerRef = useRef();
  const handleSwipe = useCallback(
    (position, action) => {
      const size = Math.max(0, position.y2 - position.y1);
      bannerRef.current.style.transform = `scale(${1 + size * 0.003})`;
      bannerRef.current.style.transition = !size ? "all linear 100ms" : "none";
      onResize(size);
    },
    [onResize]
  );
  const handleScroll = useCallback(() => {
    if (bannerRef.current) {
      const scrollY =
        document.documentElement.scrollTop || document.body.scrollTop || 0;
      if (scrollY < window.innerHeight - 260) {
        bannerRef.current.style.transform = `translateY(-${scrollY * 0.3}px)`;
        bannerRef.current.style.transition = !scrollY
          ? "all linear 100ms"
          : "none";
      }
    }
  }, [bannerRef.current]);
  const banner =
    props.banner && props.banner.indexOf("http") !== 0
      ? process.env.FILE_URL + props.banner
      : props.banner;
  const element = useMemo(() => {
    debugComponent("<Banner />", 4);
    return (
      <>
        <div className={banner ? "overlay" : ""}>
          <div ref={bannerRef} className="banner" />
          <Swiper id="banner-swiper" onSwipe={handleSwipe} />
        </div>
        {typeof window !== "undefined" ? (
          <DocumentScroll onScroll={handleScroll} />
        ) : null}
        <style jsx>{`
          .overlay {
            height: calc(100vh - 200px - 40px);
            position: fixed;
            width: 441px;
            perspective: 8px;
            perspective-origin: 50%;
          }
          .banner {
            top: -10px;
            z-index: -1;
            position: fixed;
            transform-origin: top center;
            height: calc(100vh - 200px - 40px + 10px);
            background: #dde6ef url(${banner}) no-repeat center top;
            background-size: cover;
            width: 441px;
          }
          @media only screen and (max-width: 768px) {
            .overlay,
            .banner {
              width: 100%;
              max-width: 100%;
            }
          }
        `}</style>
      </>
    );
  }, [banner]); // Dependencies required to re-render
  return element;
};

export default Banner;
