/**
 * Swiper Component for Touch Gestures
 * Supports swipe movement how far is your touch movement
 * Support swipe trigger based on length provided
 * support onClick
 * @todo use a library in replacement of this
 * @version 1.0.0
 */
import React, {
  useLayoutEffect,
  useEffect,
  useState,
  useMemo,
  useReducer,
  useRef
} from "react";
import PropTypes from "prop-types";
import { debugComponent } from "../../../lib/logger";

/**
 * Swiper actions for reducer
 */
const RESET = "Swiper/RESET";
const END = "Swiper/END";
const START = "Swiper/START";
const MOVE = "Swiper/MOVE";

/**
 * Swiper initial state
 * used in RESET
 */
const initialState = {
  x1: 0,
  x2: 0,
  y1: 0,
  y2: 0,
  id: 0,
  xr: 0,
  yr: 0
};

/**
 * Swiper Reducer, lets update state at once using reducer
 * @param {*} state
 * @param {*} action
 */
const reducer = (state, action) => {
  switch (action.type) {
    case RESET:
      return { ...initialState };
    case END:
      return { ...state, xr: state.x2 - state.x1, yr: state.y2 - state.y1 };
    case START:
      return { ...initialState, ...action.payload, xr: 0, yr: 0 };
    case MOVE:
      return { ...state, y2: action.payload.y2, x2: action.payload.x2 };
    default:
      return state;
  }
};

/**
 * Action creator
 * @param {*} type
 * @param {*} payload
 */
function action(type, payload) {
  return { type, payload };
}

/**
 * Need this only client side render
 * This should not render on server-side-rendering
 */
const Client = React.memo(props => {
  const {
    onSwipe,
    onClick,
    swipeTriggerDirection: dir,
    swipeTriggerLength: len,
    onSwipeTriggered
  } = props;
  const ref = useRef();
  const [state, dispatch] = useReducer(reducer, initialState);

  useEffect(() => {
    onSwipe({ ...state });
  }, [state.y1, state.y2, state.x1, state.x2]);

  useEffect(() => {
    handleSwipeTriggerred();
  }, [state.yr, state.xr]);

  const abs = Math.abs;
  const handleSwipeTriggerred = () => {
    const { y1, y2, x1, x2 } = state;
    const movementY = Math.max(y2 - y1);
    const movementX = Math.max(x2 - x1);
    if (["vertical", "both"].includes(dir) && abs(movementY) >= len) {
      onSwipeTriggered("vertical", movementY);
    } else if (["horizontal", "both"].includes(dir) && abs(movementX) >= len) {
      onSwipeTriggered("horizontal", movementX);
    }
  };

  const handleMouseDown = e => {
    // multi touch
    const touch = e.touches[0];
    const position = {
      y1: touch.clientY,
      y2: touch.clientY,
      x1: touch.clientX,
      x2: touch.clientX,
      id: touch.identifier
    };
    dispatch(action(START, position));
  };

  const handleMouseMove = e => {
    console.log(e)
    const touch = e.touches[0]; //.find(x => x.identifier === this.state.identifier);
    if (touch) {
      const position = {
        y2: touch.clientY,
        x2: touch.clientX
      };
      dispatch(action(MOVE, position));
    }
  };

  const handleMouseUp = e => {
    if (!e.changedTouches.length) return;
    // Always detect first touch only
    const touch = Array.from(e.changedTouches).find(x => x.identifier === 0);
    if (touch) {
      const position = {
        y2: touch.clientY,
        x2: touch.clientX
      };
      if (dir && dir !== "none") {
        dispatch(action(END, position));
      } else {
        dispatch(action(RESET, position));
      }
    }
  };

  useLayoutEffect(() => {
    if (!ref.current) return;
    ref.current.addEventListener("touchstart", handleMouseDown);
    ref.current.addEventListener("touchmove", handleMouseMove);
    ref.current.addEventListener("touchend", handleMouseUp);
    return () => {
      ref.current.removeEventListener("touchstart", handleMouseDown);
      ref.current.removeEventListener("touchmove", handleMouseDown);
      ref.current.removeEventListener("touchend", handleMouseDown);
    };
  }, []);

  debugComponent("<SwiperClient />");
  return useMemo(() => {
    debugComponent("---<SwiperClient />");
    return (
      <>
        <div onClick={onClick} className="swiper" ref={ref}>
          {props.children}
        </div>
        <style jsx>{`
          .swiper {
            z-index: 3;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            position: ${props.children ? "relative" : "absolute"};
          }
        `}</style>
      </>
    );
  }, [props.children, onClick]);
});

/**
 * Server Side Rendering doesnt need to render <Client />
 * Use this because useLayoutEffect will warn if server rendered
 */
const Swiper = props => {
  const {
    children,
    onSwipe,
    onClick,
    swipeTriggerDirection,
    swipeTriggerLength,
    onSwipeTriggered
  } = props;
  const [mounted, setMounted] = useState(false);
  useEffect(() => {
    setMounted(true);
  }, []);
  debugComponent(`<Swiper />`, children);
  return mounted ? (
    <Client
      {...{
        children,
        onSwipe,
        onClick,
        swipeTriggerDirection,
        swipeTriggerLength,
        onSwipeTriggered
      }}
    />
  ) : null;
};

Swiper.propTypes = {
  onSwipe: PropTypes.func,
  onClick: PropTypes.func,
  /**
   * vertical, horizontal, both, none
   */
  swipeTriggerDirection: PropTypes.string,
  swipeTriggerLength: PropTypes.number,
  onSwipeTriggered: PropTypes.func
};

Swiper.defaultProps = {
  onSwipe: () => {},
  onClick: () => {},
  swipeTriggerDirection: "none",
  swipeTriggerLength: 100,
  onSwipeTriggered: () => {}
};

export default React.memo(Swiper);
