import React, { useRef, useState, useCallback } from "react";
import { debugComponent } from "../../../lib/logger";
import Modal from "./Modal";
import Thumbnail from "./svg/Thumbnail";

/**
 * Render the tile card
 * @param {*} props
 * @version 1.0.0
 */
const Card = React.memo(props => {
  const { image, big, title, onClick } = props;
  const handleClick = () => {
    /**
     * @todo This will be used for animation initial position
     * ```js
     * cardRef.current.getBoundingClientRect()
     * ```
     */
    onClick(cardRef.current.getBoundingClientRect());
  };
  /**
   * Ref for the component root div
   */
  const cardRef = useRef();
  debugComponent("<Card />", 8); // Perfornmace checking if you need to check that your component doing unwanted render
  return (
    <>
      <div
        ref={cardRef}
        onClick={handleClick}
        style={{ color: "red" }}
        className="tile"
      >
        <div className="card">
          <Thumbnail image={image} />
          <div className="title">
            <h2>{title}</h2>
          </div>
        </div>
      </div>
      <style global jsx>{`
        .tile-thumbnail {
          border-radius: 19px;
          background: #f1f5f9 none no-repeat center center;
          background-size: cover;
        }
      `}</style>
      <style jsx>{`
        .card {
          box-sizing: border-box;
          box-shadow: 0 10px 35px 0 rgba(0, 0, 0, 0.13);
          border-radius: 19px;
          background-color: #ffffff;
          margin-right: 18px;
          margin-bottom: 18px;
        }
        .tile {
          width: ${big ? "100%" : "50%"};
        }
        .tile .title {
          box-sizing: border-box;
          overflow: hidden;
          text-overflow: ellipsis;
          height: ${big ? 47 : 60}px;
          padding: ${big ? 12 : 11}px 10px;
        }
        .tile h2 {
          color: red;
          margin: 0;
          font-size: 15px;
          font-weight: bold;
          letter-spacing: -0.43px;
          line-height: 19px;
          color: #262a2c;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      `}</style>
    </>
  );
});

/**
 * Render the `<Card/>` component, this does the diff on each property weather to re-render the `<Card/>` component
 * @param {object} props
 * @param {boolean} props.big
 * @param {string} props.title
 * @param {Array} props.image
 * @version 1.0.0
 */
const Tile = props => {
  /**
   * toggle flag to set modal visibility
   */
  const [modal, setModal] = useState(false);
  const { big, title } = props;

  const image =
    props.image && props.image[0] && props.image[0].url
      ? process.env.FILE_URL + props.image[0].url
      : "";
  const showModal = useCallback(() => {
    if (props.type === "Link") {
      window.open(props.url, "_blank");
    } else {
      setModal(true);
    }
  }, []);

  const hideModal = useCallback(() => {
    setModal(false);
  }, []);

  debugComponent("<Tile />", 7); // Optimize component to only do render when required
  return (
    <>
      <Card onClick={showModal} title={title} big={big} image={image} />
      <Modal onClose={hideModal} visible={modal} content={props} />
    </>
  );
};

export default Tile;
