import React, { useRef, useCallback } from "react";
import PropTypes from "prop-types";
import Handset from "./svg/Handset";
import { debugComponent } from "../../../lib/logger";
import DocumentScroll from "./DocumentScroll";

const Footer = props => {
  const { companyName, contactPersonMobile } = props;
  const actionsRef = useRef();
  const handleScroll = useCallback(() => {
    if (actionsRef.current) {
      actionsRef.current.style.top =
        (document.documentElement.scrollTop || document.body.scrollTop || 0) >
        630
          ? "calc(100vh - 67px)"
          : "";
    }
  }, [actionsRef.current]);
  debugComponent("<Footer />");
  return (
    <>
      <div className="footer">
        <div ref={actionsRef} className="actions">
          <h1>{companyName}</h1>
          <a href={`tel:${contactPersonMobile}`}>
            <Handset /> <span>Call</span>
          </a>
        </div>
      </div>
      {typeof window !== "undefined" ? (
        <DocumentScroll onScroll={handleScroll} />
      ) : null}
      <style jsx>{`
        .footer {
          height: 67px;
          position: fixed;
          z-index: 99;
          top: 100vh;
        }
        .actions {
          display: flex;
          flex-direction: row;
          box-sizing: border-box;
          align-items: center;
          top: 120vh;
          left: 0;
          right: 0;
          height: 67px;
          transition: top ease 100ms;
          padding: 0 20px;
          border-radius: 20px 20px 0 0;
          position: fixed;
          background-color: #fff;
          flex-direction: row;
          align-items: center;
          box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.15);
          margin: 0 auto;
          max-width: 441px;
        }
        .actions h1 {
          display: block;
          margin: 0;
          color: #262a2c;
          font-family: Roboto;
          font-size: 17px;
          font-weight: bold;
          letter-spacing: -0.48px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          flex: 1;
          z-index: 1;
        }
        a,
        button {
          padding: 7px 24px;
          height: 34px;
          box-sizing: border-box;
          border: none;
          border-radius: 18.5px;
          background-color: #1640e8;
          color: #ffffff;
          font-family: "Roboto", sans-serif;
          font-size: 18px;
          font-weight: 500;
          line-height: 21px;
          text-align: center;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          margin-right: 11px;
          text-decoration: none;
        }
        a span,
        button span {
          margin-left: 4px;
        }
        a.icon,
        button.icon {
          padding: 0;
          width: 34px;
        }
        @media only screen and (max-width: 768px) {
          .footer .actions {
            max-width: 100%;
          }
        }
      `}</style>
    </>
  );
};

export default Footer;
