import React, { useState, useLayoutEffect } from "react";
import ReactPlayer from "react-player";
import PropTypes from "prop-types";
import Close from "../svg/Close";
import { debugComponent } from "../../../../lib/logger";
import Swiper from "../Swiper";

const VideoContent = props => {
  const [afterShow, setAfterShow] = useState(false);
  const [playing, setPlaying] = useState(true);
  const { visible, onClose } = props;
  useLayoutEffect(() => {
    if (visible) {
      const id = setTimeout(() => {
        setAfterShow(visible);
      }, 1000);
      return () => {
        clearTimeout(id);
      };
    } else {
      setAfterShow(visible);
    }
  }, [visible]);
  const handleClick = () => {
    onClose();
  };
  const handleSwipe = (direction, movement) => {
    if (direction === "vertical") {
      onClose();
    }
    debugComponent("MultipleImageContent.swipeTriggered", 7);
  };
  const togglePlaying = e => {
    e.preventDefault();
    setPlaying(!playing);
    debugComponent("MultipleImageContent.togglePlaying", 7, !playing);
  };
  const src =
    !props.url || props.url.indexOf("http") === 0
      ? props.url
      : `${process.env.FILE_URL}${props.url}`;
  return (
    <>
      <div className="video-content">
        <ReactPlayer
          width="100%"
          height="100%"
          url={props.url}
          playing={playing && !!visible}
        />
        <Swiper
          id={`video-swiper-${props.id}`}
          onClick={togglePlaying}
          onSwipeTriggered={handleSwipe}
          swipeTriggerDirection="vertical"
          swipeTriggerLength={100}
        />
        <button className={afterShow ? "show" : ""} onClick={handleClick}>
          <Close />
        </button>
      </div>
      <style jsx>{`
        div {
          box-sizing: border-box;
          background: #000 url(${src}) no-repeat center center;
          background-size: contain;
          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          right: 0;
          margin-left: -10px;
          margin-right: -10px;
          margin-top: -34px;
        }
        button {
          opacity: 0;
          transform: scale(0, 0) rotate(180deg);
          position: absolute;
          top: 0;
          right: 0;
          margin-right: 25px;
          z-index: 1000;
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
          margin-top: 23px;
          overflow: hidden;
          border-radius: 50%;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          height: 36px;
          width: 36px;
          box-sizing: border-box;
          background-color: #fff;
          border: none;
          z-index: 5;
          transition: none;
        }
        button.show {
          transition: all linear 300ms;
          transform: scale(1, 1) rotate(0deg);
          opacity: 1;
        }
      `}</style>
      <style global jsx>{`
        .video-content iframe {
          max-width: 100%;
          width: 100%;
          height: 100%;
        }
        .video-content > div {
          max-width: 100%;
          width: 100%;
          height: 100%;
        }
      `}</style>
    </>
  );
};

export default React.memo(VideoContent);
